function Gif({ url, setShowGif }) {
  return (
    <div className="col-md-6">
      <div className="row mb-4">
        <div className="col">
          <img src={ url } alt="Gif" />
        </div>
      </div>

      <div className="row">
        <div className="col">
          <button
            type="button"
            onClick={
              (e) => {
                e.preventDefault();
                setShowGif(false);
              }
            }
          >
            Back to list
          </button>
        </div>
      </div>
    </div>
  );
}

function App() {
  const [minions, setMinions] = useState([]);
  const [showGif, setShowGif] = useState(false);
  const [selectedMinion, setSelectedMinion] = useState();

  useEffect(() => {
    // setMinions(mockedResponse);

    fetch("http://localhost:3000/minion-apis/minions.json", {
      credentials: "same-origin"
    })
    .then(rawResponse => {
      console.log("Raw response: ", rawResponse);
      return rawResponse.json();
    })
    .then(apiResponse => {
      console.log("JSON Response: ", apiResponse);
      return setMinions(apiResponse);
    });
  }, []);

  let MinionList = minions.map(minion => {
    return (
      <div className="col" key={ minion.id }>
        <div className="card h-100">
          <a
            className="text-center"
            href="/"
            onClick={
              (e) => {
                e.preventDefault();
                setSelectedMinion(minion)
                setShowGif(true);
              }
            }
          >
            <img
              className="card-img-top img-fluid w-50 mx-auto"
              src={ minion.image_url }
              alt={ `${ minion.name } pic` }
            />
          </a>

          <div className="card-body">
            <h5 className="card-title text-center">
              { minion.name }
            </h5>

            <p className="card-text">
              { minion.description }
            </p>
          </div>
        </div>
      </div>
    );
  });

  return (
    <div className="App container-fluid">
      <div className="row">
        <div className="col">
          <a className="btn btn-danger logout-btn" href="/users/sign_out" data-method="delete">Logout</a>
        </div>
      </div>

      <div className="row minions-list">
        { !showGif && MinionList }
        { !!showGif && <Gif url={ selectedMinion.gif_url } setShowGif={ setShowGif } /> }
      </div>
    </div>
  );
}

export default App;
